import React from 'react';
import './Sidebar.css';

const Sidebar = ({ onContentChange, activeContent, onPrevious, onNext }) => {
  const contents = ['Introduction', 'About', 'Services', 'Contact'];

  return (
    <div className="sidebar">
      <h2 className="sidebar-title">Menu</h2>
      <ul className="sidebar-list">
        {contents.map((item) => (
          <li
            key={item}
            className={`sidebar-item ${item === activeContent ? 'active' : ''}`}
            onClick={() => onContentChange(item)}
          >
            {item}
          </li>
        ))}
      </ul>
      <div className="navigation-buttons">
        <button onClick={onPrevious} className="nav-button">Previous</button>
        <button onClick={onNext} className="nav-button">Next</button>
      </div>
    </div>
  );
};

export default Sidebar;